.cnt-container svg .cnt-selected {
  color: #0066ff;
  fill: currentColor; }

.cnt-container svg .cnt-selectable {
  pointer-events: bounding-box; }

.cnt-container svg .cnt-selectable:hover {
  fill: #4d94ff; }

.cnt-container svg rect.cnt-areaSel {
  stroke: gray;
  stroke-dasharray: 4px;
  stroke-opacity: 0.5;
  fill: #0066ff;
  fill-opacity: .3; }

.cnt-container .cnt-emabox {
  display: none;
  font-family: Helvetica, Arial, Sans-Serif;
  font-weight: bold;
  line-height: 2em;
  background-color: #0066ff;
  color: #fff;
  padding: 5px;
  font-size: 30px;
  position: fixed;
  top: 1em;
  left: 1em; }

.cnt-container .cnt-uploadbox {
  padding: 5px;
  background-color: #0066ff;
  position: fixed;
  top: 1em;
  right: 1em; }
  .cnt-container .cnt-uploadbox .btn-file {
    position: relative;
    overflow: hidden; }
    .cnt-container .cnt-uploadbox .btn-file input[type=file] {
      position: absolute;
      top: 0;
      right: 0;
      min-width: 100%;
      min-height: 100%;
      font-size: 100px;
      text-align: right;
      filter: alpha(opacity=0);
      opacity: 0;
      outline: none;
      background: white;
      cursor: inherit;
      display: block; }
